.feedback {
  padding-bottom: 50px;
  &__title {
    text-align: center;
    font-family: 'Halvar Breitschrift';
    font-style: normal;
    font-weight: 800;
    font-size: 42px;
    margin: 0;
    margin-bottom: 60px;
    line-height: 108.5%;
    @include small-tablet{
      font-size: 28px;
    }
    @include mobile{
      font-size: 33px;
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    gap: 75px;
    margin-bottom: 60px;
    align-items: flex-start;
  }
  &__portret {
    position: relative;
    @include small-tablet{
      display: none;
    }
  }
  &__name {
    position: absolute;
    font-size: 30px;
    font-weight: 800;
    background-color: var(--blue-color);
    padding: 27px 23px;
    box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    color: white;
    margin: 0;
    font-style: normal;
    bottom: 94px;
    right: -60px;
    span{
      display: block;
      font-size: 10px;
      text-align: center;
      text-transform: uppercase;
    }
  }
  &__form {
    @include small-tablet{
      margin: 0 auto;
    }
  }
  &__form-fieldset {
    border: none;
    padding: 0;
    margin: 0;
  }
  &__form-legend {
    font-weight: 800;
    font-size: 48px;
    margin-bottom: 20px;
    @include mobile{
      font-size: 25px;
      margin-bottom: 20px;
    }
  }
  &__inputs {
    display: flex;
    flex-wrap: wrap;
    max-width: 570px;
    column-gap: 50px;
    row-gap: 40px;
    margin-bottom: 50px;
    @include mobile{
      justify-content: center;
    }
  }
  &__input {
    border-bottom: 2px solid black;
    width: 260px;
    @include mobile{
      width: 100%;
    }
  }
  &__form-text {
    font-weight: 700;
    font-size: 36px;
    margin: 0;
    margin-bottom: 20px;
    @include mobile{
      font-size: 20px;
    }
  }
  &__checkbox-container {
    display: grid;
    max-width: 560px;
   grid-template-columns: 1fr 1fr 1fr;
   row-gap: 20px;
   margin-bottom: 50px;
   column-gap: 5px;
   @include mobile{
    margin-bottom: 30px;
   }
  }
  &__checkbox {}
  &__label {}
  &__form-submit {
    color: #144890;
    font-weight: 800;
    font-size: 36px;
    line-height: 53px;
    text-decoration-line: underline;
    margin-bottom: 100px;
    @include mobile{
      font-size: 20px;
      margin-bottom: 34px;
    }
  }
  &__text {
    font-weight: 800;
    font-size: 36px;
    margin: 0;
    margin-bottom: 20px;
    @include mobile{
      font-size: 25px;
      margin-bottom: 5px;
    }
  }
  &__link {
    font-weight: 700;
    font-size: 24px;
    text-decoration-line: underline;
    color: #144890;
    @include mobile{
      font-size: 20px;
    }
  }
}
