.application {
  margin-bottom: 70px;
  &__content {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    gap: 40px;
    margin-bottom: 60px;
    @include big-desktop {
      gap: 60px;
    }
    @include small-tablet {
      flex-direction: column;
      align-items: center;
    }
  }
  &__portret {
    background: #f5f6f9;
    border-radius: 20px;
    width: 580px;
    height: 325px;
    position: relative;
    @include tablet {
      min-width: 370px;
      width: 370px;
    }
    @include small-tablet {
      width: 100%;
    }
    @include mobile{
      height: 200px;
    }
  }
  &__img {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    @include mobile{
      height: 260px;
    }
  }
  &__quote {
    max-width: 495px;
    position: relative;
    &::after {
      display: block;
      content: "";
      position: absolute;
      left: -110px;
      top: -10px;
      height: 63px;
      width: 78px;
      background-image: url(../img/quote.svg);
      background-repeat: no-repeat;
      @include tablet {
        left: -60px;
        height: 111px;
        width: 47px;
      }
      @include small-tablet{
        top: -65px;
        left: 0;
      }
    }
  }
  &__quote-text {
    font-family: "Halvar Breitschrift";
    font-style: normal;
    font-weight: 800;
    font-size: 24px;
    text-decoration: none;
    quotes: none;
    margin-bottom: 55px;
    display: block;
    @include mobile {
      font-size: 18px;
      margin-bottom: 4px;
    }
  }
  &__quote-author {
    display: block;
    font-style: normal;
    max-width: 284px;
    font-weight: 400;
    font-size: 18px;
    color: rgba(38, 37, 42, 0.7);
    @include small-tablet{
      width: 100%;
      font-size: 14px;
    }
  }
  &__info-row {
    display: flex;
    gap: 15px;
    @include mobile{
      flex-direction: column;
    }
  }
  &__info {
    background-color: var(--pink-color);
    border-radius: 30px;
    padding: 40px;
    @include tablet{
      padding: 24px 20px;
      width: 100%;
      border-radius: 20px;
    }
  }
  &__info-title {
    font-family: "Halvar Breitschrift";
    font-style: normal;
    font-weight: 800;
    font-size: 40px;
    color: white;
    margin: 0;
    margin-bottom: 100px;
    @include tablet{
      font-size: 30px;
      margin-bottom: 40px;
    }
    @include mobile {
      font-size: 36px;
      margin-bottom: 44px;
    }
  }
  &__info-text {
    max-width: 694px;
    font-weight: 700;
    font-size: 24px;
    color: #ffffff;
    line-height: 140%;
    margin: 0;
    @include tablet{
      font-size: 16px;
    }
    @include mobile {
      font-size: 14px;
    }
  }
  &__info-img {
    border-radius: 30px;
    @include tablet{
      width: 50%;
    }
    @include mobile{
      width: 100%;
      height: 290px;
      border-radius: 20px;
    }
  }
}
