.completed {
  margin-bottom: 70px;
  @include mobile{
    margin-bottom: 44px;
  }
  &__container {
    @include mobile {
      padding: 0;
    }
  }
  &__title {
    margin-bottom: 25px;
    @include mobile {
      padding-left: 15px;
    }
  }
  &__slider {
    background-color: var(--blue-color);
    padding: 40px;
    border-radius: 30px;
    position: relative;
    @include mobile {
      padding: 20px;
      border-radius: 20px;
    }
  }
  &__swiper {
  }
  &__swiper-wrapper {
  }
  &__swiper-slide {
    height: auto;
  }
  &__swiper-card {
    background-color: white;
    padding: 40px;
    border-radius: 30px;
    height: calc(100% - 80px);
    @include tablet{
    }
    @include mobile {
      padding: 20px;
      border-radius: 20px;
      height: calc(100% - 60px);
    }
  }
  &__swiper-title {
    font-family: "Halvar Breitschrift";
    font-style: normal;
    font-weight: 800;
    font-size: 24px;
    margin: 0;
    margin-bottom: 20px;
    @include mobile {
      font-size: 24px;
    }
  }
  &__swiper-text {
    font-weight: 700;
    font-size: 18px;
    color: rgba(38, 37, 42, 0.7);
    margin: 0;
    margin-bottom: 10px;
    @include mobile {
      font-size: 18px;
    }
  }
  &__swiper-list {
    padding: 0 0 0 30px;
  }
  &__swiper-item {
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    color: rgba(38, 37, 42, 0.7);
    @include mobile {
      font-size: 16px;
    }
  }
}
.completed__swiper-navigation {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
  align-items: center;
  @include mobile {
    margin-right: 10px;
    margin-left: 10px;
  }
}
.completed__swiper-buttons {
  display: flex;
  flex-direction: row-reverse;
}
.completed__swiper-pagination {
  font-family: "Halvar Breitschrift";
  font-weight: 800;
  font-size: 40px;
  color: #ffffff;
  display: block;
  @include mobile {
    font-size: 36px;
    transform: translateY(-10px);
  }
  .swiper-pagination-current {
    margin-right: -10px;
  }
  .swiper-pagination-total {
    margin-left: -10px;
  }
}
.completed__swiper-prev {
  background-image: url(../img/prev-swiper-btn.svg);
  width: 90px;
  height: 60px;
  display: block;
  position: relative;
  background-repeat: no-repeat;
  &::after {
    content: "";
  }
  @include mobile {
    width: 84px;
    height: 54px;
  }
}
.completed__swiper-next {
  background-image: url(../img/next-swiper-btn.svg);
  width: 90px;
  height: 60px;
  display: block;
  position: relative;
  background-repeat: no-repeat;
  &::after {
    content: "";
  }
  @include mobile {
    width: 84px;
    height: 54px;
    margin-right: -10px;
  }
}
