.direction {
  margin-bottom: 60px;
  @include mobile {
    margin-bottom: 44px;
  }
  &__container {
    @include mobile {
      padding: 0;
    }
  }
  &__content {
    background-color: var(--pink-color);
    padding: 60px 40px 40px 40px;
    border-radius: 40px;
    position: relative;
    overflow: hidden;
    background-image: url(../img/direction-img.webp);
    background-repeat: no-repeat;
    background-position: center -70px;
    @include small-tablet {
      background-position: bottom;
    }
    @include mobile {
      background-image: unset;
      padding: 24px 20px;
      border-radius: 20px;
    }
  }
  &__title {
    max-width: 575px;
    color: #ffffff;
    @include tablet {
      margin-bottom: 40px;
    }
    @include mobile {
      font-size: 36px;
      margin-bottom: 28px;
    }
  }
  &__text {
    max-width: 240px;
    font-weight: 800;
    font-size: 24px;
    color: #ffffff;
    display: block;
    margin: 0;
    margin-left: auto;
    margin-bottom: 38px;
    @include mobile {
      margin-bottom: 28px;
      font-size: 14px;
      max-width: 100%;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 15px;
    @include tablet {
      grid-template-columns: repeat(3, 1fr);
    }
    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__item {
    background-color: white;
    padding: 30px;
    border-radius: 30px;
    @include mobile {
      padding: 20px;
      border-radius: 20px;
    }
  }
  &__card-number {
    background-color: var(--blue-color);
    margin: 0;
    display: inline-block;
    font-family: "Halvar Breitschrift";
    font-style: normal;
    font-weight: 800;
    font-size: 32px;
    color: white;
    padding: 5px 27px;
    border-radius: 25px;
    margin: 0;
    margin-bottom: 50px;
    @include mobile {
      font-size: 24px;
      padding: 5px 23px;
    }
  }
  &__card-text {
    font-weight: 700;
    font-size: 24px;
    margin: 0;
    @include big-desktop {
      font-size: 18px;
    }
    @include mobile {
      font-size: 24px;
    }
  }
  &__bg-img {
    position: absolute;
    top: -70px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 30;
  }
}
