.resume {
  margin-bottom: 15px;
  &__content {
    display: flex;
    gap: 10px;
    justify-content: space-between;
    @include small-tablet{
      flex-direction: column;
    }
  }
  &__info {
    background-color: #F5F6F9;
    padding: 40px;
    border-radius: 30px;
    max-width: 800px;
    @include mobile{
      border-radius: 20px;
      padding: 20px;
    }
  }
  &__title {
    margin-bottom: 100px;
    @include mobile{
      font-size: 36px;
      margin-bottom: 44px;
      border-radius: 20px;
    }
  }
  &__info-text {
    font-weight: 700;
    font-size: 24px;
    margin: 0;
    @include tablet{
      font-size: 20px;
    }
    @include mobile{
      font-size: 18px;
    }
  }
  &__final {
    padding: 40px;
    border-radius: 30px;
    background-image: url(../img/resume-img-bg-1.webp);
    background-repeat: no-repeat;
    background-size: cover;
    @include mobile{
      border-radius: 20px;
      padding: 20px;
    }
  }
  &__final-title {
    color: white;
    margin-bottom: 66px;
    @include mobile{
      font-size: 36px;
      margin-bottom: 44px;
    }
  }
  &__final-text {
    font-weight: 800;
    font-size: 24px;
    color: #FFFFFF;
    @include tablet{
      font-size: 20px;
    }
    @include mobile{
      font-size: 18px;
    }
  }
}
