.recommend {
  margin-bottom: 60px;
  &__container {
    @include mobile {
      padding: 0;
    }
  }
  &__content {
    background-color: var(--pink-color);
    padding: 60px 40px 60px 40px;
    border-radius: 42px;
    background-image: url(../img/recommend-bg.webp);
    background-repeat: no-repeat;
    background-position: -270px 50%;
    @include tablet {
      background-position: bottom;
    }
    @include small-tablet {
      padding: 60px 25px 60px 25px;
      background-image: unset;
    }
    @include mobile {
      border-radius: 20px;
      padding: 24px 20px;
    }
  }
  &__text {
    font-family: "Halvar Breitschrift";
    font-style: normal;
    font-weight: 800;
    font-size: 40px;
    color: white;
    max-width: 610px;
    margin: 0;
    margin-left: auto;
    margin-bottom: 164px;
    @include small-tablet {
      font-size: 30px;
      margin-bottom: 28px;
    }
  }
  &__list {
    display: flex;
    justify-content: space-between;
    gap: 15px;
    @include small-tablet {
      flex-wrap: wrap;
    }
  }
  &__item {
    background: #ffffff;
    border-radius: 30px;
    padding: 40px;
    @include small-tablet {
      width: 100%;
    }
    @include mobile {
      border-radius: 20px;
    }
  }
  &__item-title {
    font-family: "Halvar Breitschrift";
    font-weight: 800;
    font-size: 24px;
    margin: 0;
    margin-bottom: 20px;
    @include tablet {
      font-size: 18px;
    }
    @include small-tablet {
      font-size: 24px;
      margin-bottom: 44px;
    }
  }
  &__item-text {
    font-weight: 700;
    font-size: 18px;
    color: rgba(38, 37, 42, 0.7);
    margin: 0;
    @include tablet {
      font-size: 16px;
    }
    @include small-tablet {
      font-size: 18px;
    }
  }
}
