.result {
  margin-bottom: 60px;
  &__info {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
    gap: 20px;
    @include tablet {
      flex-direction: column;
      margin-bottom: 50px;
    }
  }
  &__title {
  }
  &__info-text {
    font-family: "Halvar Breitschrift";
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    max-width: 800px;
    margin: 0;
    @include mobile {
      font-size: 14px;
    }
  }
  &__progress {
    display: flex;
    gap: 15px;
    margin-bottom: 15px;
    @include small-tablet {
      flex-direction: column;
      align-items: center;
    }
  }
  &__progress-info {
    background-color: var(--pink-color);
    border-radius: 30px;
    padding: 40px;
    @include tablet {
      max-width: 700px;
    }
    @include small-tablet {
      width: 100%;
    }
    @include mobile {
      min-height: 250px;
      padding: 24px 20px;
      border-radius: 20px;
    }
  }
  &__progress-title {
    font-family: "Halvar Breitschrift";
    font-weight: 800;
    font-size: 40px;
    margin: 0;
    margin-bottom: 40px;
    color: white;
    @include tablet {
      font-size: 25px;
    }
    @include mobile {
      font-size: 24px;
      margin-bottom: 44px;
    }
  }
  &__progress-text {
    font-weight: 700;
    font-size: 24px;
    color: #ffffff;
    margin: 0;
    margin-bottom: 10px;
    @include tablet {
      font-size: 16px;
    }
    @include mobile {
      font-size: 18px;
    }
  }
  &__progress-graph {
    position: relative;
    svg {
      max-width: 805px;
      height: 520px;
      @include tablet {
        width: 530px;
        height: 100%;
      }
      @include mobile {
        width: 320px;
      }
    }
  }
  &__progress-reverse {
    flex-direction: row-reverse;
    @include small-tablet {
      align-items: center;
      flex-direction: column;
    }
  }
  &__after {
    border-radius: 30px;
    padding: 40px;
    background-color: var(--blue-color);
    @include small-tablet {
      padding: 20px 24px;
    }
    @include mobile {
      border-radius: 20px;
    }
  }
  &__after-info {
    text-align: center;
    font-family: "Halvar Breitschrift";
    font-weight: 800;
    font-size: 24px;
    color: #ffffff;
    margin: 0;
    margin-bottom: 40px;
    line-height: 140%;
    @include small-tablet {
      font-size: 18px;
    }
  }
  &__after-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    @include tablet {
      flex-direction: column;
    }
  }
  &__after-text {
    font-family: "Halvar Breitschrift";
    font-weight: 800;
    font-size: 130px;
    text-align: center;
    color: #ffffff;
    max-width: 395px;
    @include small-tablet {
      font-size: 80px;
    }
    @include mobile {
      font-size: 40px;
    }
    p {
      font-size: 40px;
      text-align: center;
      color: rgba(255, 255, 255, 0.6);
      margin: 0;
      transform: translateY(-40px);
      @include small-tablet {
        font-size: 30px;
      }
      @include mobile {
        font-size: 18px;
        line-height: 140%;
        transform: translateY(0);
      }
    }
  }
  &__after-span {
    transform: translateY(-60px);
    display: block;
    @include mobile {
      transform: translateY(0);
      display: inline;
    }
  }
  &__after-graph {
    svg {
      width: 805px;
      @include big-desktop {
        width: 650px;
      }
      @include small-tablet {
        width: 480px;
      }
      @include mobile {
        width: 270px;
      }
    }
  }
}
.container {
}
.bold-number {
  font-size: 220px;
  @include small-tablet {
    font-size: 150px;
  }
  @include mobile {
    font-size: 40px;
  }
}
.section-title {
}
.graph-info-red,
.graph-info-blue {
  background-color: var(--blue-color);
  display: inline-block;
  border-radius: 10px;
  font-weight: 700;
  font-size: 14px;
  color: #ffffff;
  max-width: 200px;
  padding: 15px 20px;
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  transition: opacity .3s ease-in;
  opacity: 0;
  &--active {
    opacity: 1;
    overflow: visible;
    margin: unset;
    border: unset;
    padding: 15px 20px;
    width: unset;
    height: unset;
    clip: unset;
    right: 60px;
    bottom: 320px;
    @include small-tablet {
      bottom: 200px;
    }
    @include tablet {
      bottom: 90px;
    }
    @include mobile {
      bottom: 100px;
      font-size: 14px;
      padding: 15px 20px;
    }
  }
}
.graph-info-red {
  background-color: var(--pink-color);
  bottom: 200px;
  @include tablet {
    bottom: 70px;
    right: 90px;
  }
  @include small-tablet {
    bottom: 120px;
  }
  @include mobile {
    bottom: 40px;
    font-size: 14px;
    padding: 15px 20px;
  }
}
.graph-info-blue-ya {
  @include tablet {
    bottom: 180px;
  }
  @include small-tablet {
    bottom: 200px;
  }
  @include mobile {
    bottom: 150px;
    right: 100px;
  }
}
.graph-info-red-ya {
  bottom: 50px;
  @include small-tablet {
    bottom: 30px;
  }
}
.graph-blue-mark {
  cursor: pointer;
}
.graph-red-mark {
  cursor: pointer;
}
.result__progress-graph-desk {
  @include mobile {
    display: none;
  }
}
.result__progress-graph-mob {
  display: none;
  @include mobile {
    display: block;
    width: 100%;
    height: auto;
  }
}
