.hero {
  margin-bottom: 60px;
  padding-top: 50px;
  &__content {
  }
  &__info {
    padding: 40px;
    background: #f5f6f9;
    border-radius: 40px;
    @include mobile {
      padding: 24px 20px;
      border-radius: 20px;
    }
  }
  &__container{
    @include mobile{
      padding: 0;
    }
  }
  &__info-top {
    display: flex;
    gap: 40px;
    margin-bottom: 60px;
    @include mobile{
      gap: 30px;
    }
  }
  &__title {
    font-weight: 400;
    font-size: 24px;
    max-width: 212px;
    margin: 0;
    span {
      color: var(--blue-color);
    }
    @include mobile {
      font-size: 18px;
    }
  }
  &__info-text {
    font-weight: 400;
    font-size: 18px;
    max-width: 288px;
    margin: 0;
    @include mobile {
      font-size: 14px;
      max-width: 120px;
      line-height: 140%;
    }
  }
  &__project-name {
    font-family: "Halvar Breitschrift";
    font-weight: 700;
    font-size: 180px;
    margin: 0;
    @include tablet {
      font-size: 130px;
    }
    @include small-tablet {
      font-size: 95px;
    }
    @include mobile {
      font-size: 66px;
    }
  }
  &__rating {
    background-color: var(--blue-color);
    padding: 40px;
    border-radius: 40px;
    position: relative;
    @include mobile{
      border-radius: 20px;
      padding: 24px 20px;
    }
  }
  &__rating-info {
  }
  &__rating-top {
    display: flex;
    gap: 64px;
    @include mobile {
      gap: 35px;
    }
  }
  &__rating-position {
  }
  &__rating-position-number {
    font-family: "Halvar Breitschrift";
    font-style: normal;
    font-weight: 700;
    font-size: 56px;
    color: #ffffff;
    margin: 0;
    margin-bottom: 5px;
    @include small-tablet {
      font-size: 40px;
    }
    @include mobile {
      font-size: 24px;
    }
    span {
      background: rgba(255, 255, 255, 0.2);
      border-radius: 20px;
      padding: 5px;
      margin-left: -13px;
      @include mobile {
        margin-left: -0px;
        padding: 4px 8px;
        border-radius: 10px;
      }
    }
  }
  &__rating-position-text {
    font-weight: 700;
    font-size: 18px;
    color: #ffffff;
    margin: 0;
    margin-bottom: 57px;
    @include mobile{
      margin-bottom: 18px;
      font-size: 14px;
    }
  }
  &__rating-text {
    max-width: 480px;
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    color: white;
    @include mobile {
      font-size: 14px;
      line-height: 21px;
    }
  }
  &__img {
    position: absolute;
    right: 40px;
    bottom: 0;
    @include tablet {
      display: none;
    }
  }
}
