.purpose {
  margin-bottom: 160px;
  @include big-desktop {
    margin-bottom: 100px;
  }
  @include small-tablet {
    margin-bottom: 150px;
  }
  @include mobile {
    margin-bottom: 100px;
  }
  &__content {
    background-color: var(--blue-color);
    padding: 40px 152px 175px 152px;
    width: 100%;
    border-radius: 30px;
    @include tablet {
      padding: 40px 100px 175px 100px;
    }
    @include mobile {
      padding: 24px 20px;
      border-radius: 20px;
    }
  }
  &__title {
    text-align: center;
    font-weight: 700;
    font-size: 24px;
    color: #ffffff;
    margin: 0;
    margin-bottom: 105px;
    @include mobile {
      font-size: 14px;
      margin-bottom: 20px;
    }
  }
  &__text {
    font-family: "Halvar Breitschrift";
    font-weight: 800;
    font-size: 42px;
    text-align: center;
    color: #ffffff;
    margin: 0;
    @include tablet {
      font-size: 35px;
    }
    @include mobile {
      font-size: 24px;
    }
  }
}
