.about {
  margin-bottom: 60px;
  &__title {
    margin-bottom: 25px;
  }
  &__content {
    display: flex;
    gap: 15px;

    @include big-desktop {
      justify-content: center;
    }
    @include small-tablet {
      flex-wrap: wrap-reverse;
    }
    @include mobile {
      flex-wrap: wrap;
    }
  }
  &__info {
    background-color: #f5f6f9;
    border-radius: 30px;
    padding: 40px;
    min-height: 352px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include mobile{
      padding: 20px;
      border-radius: 20px;
    }
    &:nth-child(1) {
      max-width: 310px;
      @include big-desktop {
        max-width: 480px;
      }
      @include small-tablet {
        width: 100%;
        max-width: 100%;
      }
      @include mobile {
        width: 100%;
        min-height: 185px;
      }
    }
    &:nth-child(2) {
      max-width: 480px;
      @include small-tablet {
        width: 50%;
      }
      @include mobile {
        max-width: 100%;
        width: 100%;
        min-height: 161px;
      }
    }
    &:nth-child(3) {
      max-width: 480px;
      background-image: url(../img/about-bg-card.webp);
      background-repeat: no-repeat;
      color: white;
      background-size: cover;
      @include small-tablet {
        width: 45%;
      }
      @include mobile {
        max-width: 100%;
        width: 100%;
        min-height: 161px;
      }
      .about__info-info {
        color: white;
      }
    }
  }
  &__info-title {
    font-family: "Halvar Breitschrift";
    font-style: normal;
    font-weight: 800;
    font-size: 40px;
    margin: 0;
    @include big-desktop {
      font-size: 27px;
    }
    @include tablet {
      font-size: 17px;
    }
    @include mobile {
      font-size: 24px;
    }
  }
  &__info-info {
    font-weight: 700;
    font-size: 24px;
    color: rgba(38, 37, 42, 0.7);
    margin: 0;
    @include tablet {
      font-size: 20px;
    }
    @include mobile {
      font-size: 18px;
      max-width: 230px;
    }
  }
}
